.Svg {
  display: inline-block;
  vertical-align: top;
  position: relative; }

svg {
  pointer-events: none; }

.Button {
  position: relative;
  display: inline-block;
  vertical-align: top;
  color: $black;
  fill: $black;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  white-space: nowrap;
  text-rendering: auto;
  user-select: none;
  box-sizing: border-box;
  letter-spacing: 0;
  font-weight: 700;
  font-style: normal;
  box-shadow: none;

  $root: &;

  &:hover,
  &:focus {
    outline: none; }

  &__icon {
    position: relative; } }
