@mixin clearfix {
  &:before,
  &:after {
    content: "";
    display: table; }
  &:after {
    clear: both; } }

@mixin placeholder {
  $placeholders: ':-moz' ':-webkit-input' '-moz' '-ms-input';
  @each $placeholder in $placeholders {
    &:#{$placeholder}-placeholder {
      @content; } } }

@mixin phablet {
  @media screen and (min-width: $phablet) {
    @content; } }

@mixin tablet {
  @media screen and (min-width: $tablet) {
    @content; } }

@mixin desktop {
  @media screen and (min-width: $desktop) {
    @content; } }
