// 1. Initial variables

// Colors

$black: #000000 !default;
// $gray: #808080 !default
// $light: #9e9e9e !default
// $lighter: #f0f0f0 !default
$white: #ffffff !default;

$red: #dd0031;
$text: #2c3778;

// Typography

$family-sans-serif: "Tenso W00", "Helvetica Neue", "Helvetica", "Arial", sans-serif !default;
$family-base: $family-sans-serif !default;

// Scaffolding

$body-background: $red !default;
$body-rendering: optimizeLegibility !default;
$body-family: $family-sans-serif !default;
$body-text: $text !default;
$body-line-height: 2;
$body-weight: 400 !default;
$body-size: 16px !default;

$heading-line-height: 1.22;

// Breakpoints

$phablet: 600px !default;
$tablet: 768px !default;
$desktop: 960px !default;

// Miscellaneous

$easing: ease-out !default;
$speed: 86ms !default;
